import React from 'react'

class List extends React.Component{
  render(){
    let list = this.props.data.items.map((a) => {
      return <li><a href={a.target}>{a.text}</a></li>;
    })

    return(
        <ul className='Footer__list'>
         <li><h4 className='list__header'>{this.props.data.heading}</h4></li>
         {list}
        </ul>
    )
  }
}


export default List;
